import * as THREE from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';


// De scene
var scene = new THREE.Scene();
scene.background = new THREE.Color('white');

// Renderer die de grootte van de container gebruikt en daarin wordt geplaatst
const container = document.getElementById('canvas-container');
const renderer = new THREE.WebGLRenderer(
  { antialias: true, alpha: true, powerPreference: "high-performance",  canvas: container.querySelector('canvas') });
renderer.setSize(container.clientWidth, container.clientHeight);
renderer.setPixelRatio(window.devicePixelRatio);

// Update canvas bij resize:
window.addEventListener('resize', () => {
  const width = container.clientWidth;
  const height = container.clientHeight;
  renderer.setSize(width, height);
  camera.aspect = width / height;
  camera.updateProjectionMatrix();
});

// Camera die de grootte van de container gebruikt
var camera = new THREE.PerspectiveCamera(
  70,
  container.clientWidth / container.clientHeight,
  0.1,
  1000
);
camera.position.set(1.2, 1.2, 1.2);
camera.lookAt(scene.position);

// OrbitControls instellen
var controls = new OrbitControls(camera, renderer.domElement);
controls.enableDamping = true; // Schakel demping in voor soepelere beweging
controls.dampingFactor = 0.25;
controls.screenSpacePanning = false;

// Verlichting toevoegen
const ambientLight = new THREE.AmbientLight(0xf6cd8b, 2.0);
const light = new THREE.DirectionalLight(0xFFFFFF, 2.0);
light.position.set(2.5, 5, 5);
light.castShadow = true;
light.shadow.mapSize.width = 2048;
light.shadow.mapSize.height = 2048;
light.shadow.camera.near = 0.5;
light.shadow.camera.far = 50;
light.shadow.camera.left = -10;
light.shadow.camera.right = 10;
light.shadow.camera.top = 10;
light.shadow.camera.bottom = -10;
renderer.shadowMap.enabled = true;
renderer.shadowMap.type = THREE.PCFSoftShadowMap; // Dit zorgt voor zachte schaduwen
scene.add(ambientLight, light);

// Voeg een axis helper toe
//scene.add(new THREE.AxesHelper(2));


const OSBColorMat = new THREE.MeshStandardMaterial({ color:0x236C2E });
const KeperColorMat = new THREE.MeshStandardMaterial({ color: 0xa28338 });
const PlankColorMat = new THREE.MeshStandardMaterial({ color: 0xA28338 });

const edgeMaterial = new THREE.LineBasicMaterial({ color: 0x000000 });


//-------------Functie om een object met randen te maken--------------
function createBoxWithEdges(geometry, material) {
    const mesh = new THREE.Mesh(geometry, material);
    mesh.castShadow = true;
    const edges = new THREE.EdgesGeometry(geometry);
    const line = new THREE.LineSegments(edges, edgeMaterial);
    mesh.add(line);
    return mesh;
}

//--------onderdelen------------
let Kepers = [];
let Bodems = [];
let Koppen = [];
let ZijKanten = [];
let KopStaanders = [];
let KopRegels = [];
let ZijkantStaanders = [];
let DekselLiggers = [];
let Deksels = [];

//schaduwvlak
let ground;


//----Variabelen-----
let Lengte;
let Breedte;
let Hoogte;
let PlaatDikte = 18/1000;
let PlankDikte = 20/1000;
let PlankBreedte = 75/1000;
let KeperBreedte = 80/1000;
let KeperHoogte = 100/1000;
let AantalKepersIn;
let KeperAfstand;
let KeperAantal;
let KeperKopAfstand = 100/1000;
let MaxOverSpanning = 0.6;
let KopDikte = PlaatDikte + PlankDikte;
var StartZ;
var Spacing;
var DekselLiggerBreedte;
var KeperHefAfstand

let PlaatBreedte = 1.2;
let PlaatLengte = 2.4;



//***************Variabelen inlezen******************

function VariabelenInlezen(){ 
    Lengte = parseFloat(document.getElementById('LengteInput').value)/1000;
    Breedte = parseFloat(document.getElementById('BreedteInput').value)/1000;
    Hoogte = parseFloat(document.getElementById('HoogteInput').value)/1000;
    AantalKepersIn = parseFloat(document.getElementById('KeperAantalInput').value);
    KeperAfstand = parseFloat(document.getElementById('KeperAfstandInput').value)/1000;

    Lengte = Math.min(Lengte, PlaatLengte);
    Breedte = Math.min(Breedte, PlaatBreedte);
    Hoogte = Math.min(Hoogte, PlaatBreedte);

    Lengte = Math.max(Lengte,  500/1000, AantalKepersIn*KeperBreedte - 2*KopDikte);
    Breedte = Math.max(Breedte,  300/1000);
    Hoogte = Math.max(Hoogte, 300/1000);


    Lengte = (Math.round(Lengte*100))/100;
    Breedte = Math.round(Breedte*100)/100;
    Hoogte = Math.round(Hoogte*100)/100;

    //overmeten maat
   let LLengte = Lengte+ 2*KopDikte;
   let BBreedte = Breedte + 2* PlaatDikte;
   let HHoogte = Hoogte + 2*PlaatDikte +PlankDikte + KeperHoogte;

    document.getElementById("LLengte").textContent = (LLengte*1000).toFixed(0);
    document.getElementById("BBreedte").textContent = (BBreedte*1000).toFixed(0);
    document.getElementById("HHoogte").textContent = (HHoogte*1000).toFixed(0);  
    

//Variabelen Lengte,Breedte, Hoogte schrijven naar de HTML waarden.
    document.getElementById("BreedteInput").value = (Breedte*1000).toFixed(0);
    document.getElementById("LengteInput").value = (Lengte*1000).toFixed(0);
    document.getElementById("HoogteInput").value = (Hoogte*1000).toFixed(0);  
}
//***************oude onderdelenen verwijderen******************

function OnderdelenDeleten(){
    
    Kepers.forEach(a => scene.remove(a));
    ZijkantStaanders.forEach(a => scene.remove(a));
    DekselLiggers.forEach(a => scene.remove(a));
    KopStaanders.forEach(a => scene.remove(a));
    KopRegels.forEach(a => scene.remove(a));
    Bodems.forEach(a => scene.remove(a));
    Koppen.forEach(a => scene.remove(a));
    ZijKanten.forEach(a => scene.remove(a));
    Deksels.forEach(a => scene.remove(a));

}

//-------------Functie om een object met randen te maken--------------
function createBoxWithEdges(geometry, material) {
    const mesh = new THREE.Mesh(geometry, material);
    mesh.castShadow = true;
    const edges = new THREE.EdgesGeometry(geometry);
    const line = new THREE.LineSegments(edges, edgeMaterial);
    mesh.add(line);
    return mesh;
}

function Bodem_GEN(){

  let Bodem = createBoxWithEdges(new THREE.BoxGeometry(Breedte,PlaatDikte,Lengte + 2*KopDikte), OSBColorMat);
  Bodem.position.set(0, PlaatDikte/2, 0);
  
  Bodems.push(Bodem);
  scene.add(Bodem);

}

function Keper_GEN(){



  if (document.getElementById('KeperAfstandCheck').checked == false) {

    KeperAantal = Math.max(2,Math.ceil(( Lengte +2*KopDikte - 2*KeperKopAfstand) / (MaxOverSpanning)));
     
    StartZ = - Lengte/2 - KopDikte + KeperKopAfstand + KeperBreedte/2;

    Spacing = -2*(- Lengte/2 - KopDikte + KeperKopAfstand + KeperBreedte/2)/(KeperAantal - 1);
     
  } 

  else {

      KeperAantal = AantalKepersIn
      KeperAantal = Math.max(2,KeperAantal);
      document.getElementById('KeperAantalInput').value = KeperAantal;// Terugsturen van de Aantalkeper waarde naar HTML


      KeperAfstand = Math.min(Lengte, KeperAfstand);  //KeperAfstand zijn maximum waarde begrenzen
      KeperAfstand = Math.max(KeperAfstand,KeperAantal*KeperBreedte) //KeperAfstand zijn minimum begrenzen


      document.getElementById('KeperAfstandSlider').max = (Lengte)*1000;      //De maximum waade van de slider terugsturen naar html
      document.getElementById('KeperAfstandSlider').min = KeperAantal*KeperBreedte*1000; //De miniumum waarde van de slider terugsturen naar HTML
      document.getElementById('KeperAfstandInput').value = (KeperAfstand*1000).toFixed(0); // De value van KeperAfstand terugsturen zodat deze aanpast aan de gestelde grenzen
      KeperAfstand=KeperAfstand-KeperBreedte;   // Compenseren voor de keperbreedte

      var MinAfstand = (KeperAantal - 1)*KeperBreedte;
      StartZ = - Math.max(KeperAfstand/2,MinAfstand/2);
      
      Spacing = Math.max(KeperAfstand,MinAfstand) / (KeperAantal - 1)
      console.log(KopDikte*2000);
  }

  // Kepers genereren
  for (let i = 0; i < KeperAantal; i++) {

      let zPos = StartZ + i * Spacing;
  
      let Keper = createBoxWithEdges(new THREE.BoxGeometry(Breedte + 2*PlaatDikte, KeperHoogte, KeperBreedte), KeperColorMat);
  
      // Positioneren onder de kist
      Keper.position.set(0, -KeperHoogte/2, zPos);
  
      // Toevoegen aan de scene en de array
      scene.add(Keper);
      Kepers.push(Keper);
  }


  //hefafstand uitrekenen en terugsturen naar html
    if (KeperAantal % 2 == 0){

      KeperHefAfstand = Spacing - KeperBreedte;
    }else{

      KeperHefAfstand = 2*Spacing - KeperBreedte;
    }
     
    document.getElementById("KeperHefAfstand").textContent = (KeperHefAfstand*1000).toFixed(0);

}

function Koppen_GEN(){

  //-----------------------koppen-----------------------------
     for (let i = 0; i < 2; i++) {
      
  
      let Kop = createBoxWithEdges(new THREE.BoxGeometry(Breedte, Hoogte - PlaatDikte, PlaatDikte), OSBColorMat);
  
          // Positie van de plaat instellen
          let zPositie = ((Lengte + PlaatDikte)/2);
          if (i === 1) {
              zPositie = -((Lengte + PlaatDikte))/2; // Maak de z-positie negatief voor de tweede plaat
          }
      
          Kop.position.set(0, Hoogte/2 + PlaatDikte/2, zPositie);
  
  
      // Toevoegen aan de scene en de array
      scene.add(Kop);
      Koppen.push(Kop);
  }

    //----------Staanders-------------
  for (let i = 0; i < 4; i++) {
      // Maak een staander
      let Staander = createBoxWithEdges(new THREE.BoxGeometry(PlankBreedte,Hoogte - PlaatDikte,PlankDikte), PlankColorMat);
  
      // Bepaal de x- en z-positie voor de vier hoeken
      let xPositie = (i === 0 || i === 1) ? (Breedte / 2  - PlankBreedte/2): -(Breedte / 2- PlankBreedte/2);
      let zPositie = (i === 0 || i === 2) ? (Lengte / 2  + PlaatDikte + PlankDikte/2) : -(Lengte / 2  + PlaatDikte + PlankDikte/2);
      
      // Positie van de staander instellen
      Staander.position.set(xPositie, Hoogte/2 + PlaatDikte/2, zPositie);
  
      // Toevoegen aan de scene en de array
      scene.add(Staander);
      KopStaanders.push(Staander);
  }

   //----------regels------------
  for (let i = 0; i < 4; i++) {
      // Maak een regel
      let Regel = createBoxWithEdges(new THREE.BoxGeometry(Breedte - 2*PlankBreedte,PlankBreedte,PlankDikte), PlankColorMat);
  
      // Bepaal de x- en z-positie voor de vier hoeken
      let yPositie = (i === 0 || i === 1) ? (Hoogte - PlankBreedte/2): (PlankBreedte/2 + PlaatDikte);
      let zPositie = (i === 0 || i === 2) ? (Lengte/2 + KopDikte - PlankDikte/2) : -(Lengte/2 + KopDikte - PlankDikte/2);
      
      // Positie van de staander instellen
      Regel.position.set(0, yPositie, zPositie);
  
      // Toevoegen aan de scene en de array
      scene.add(Regel);
      KopRegels.push(Regel);
  }
}

function ZijKanten_GEN(){

  //----------------zijkanten-------------
  const ZijKantBreedte = Lengte + 2*KopDikte;
  const ZijKantHoogte = Hoogte;
      
    for (let richting of [1,-1]) {

    let ZijKant = createBoxWithEdges(new THREE.BoxGeometry(PlaatDikte,ZijKantHoogte,ZijKantBreedte), OSBColorMat);

    ZijKant.position.set(richting*(Breedte + PlaatDikte)/2,Hoogte/2,0)

    // Toevoegen aan de scene en de array
    scene.add(ZijKant);
    ZijKanten.push(ZijKant);
  }

  
for (let i = 0; i < KeperAantal; i++) {

  const hoogteStaander = Hoogte + PlaatDikte + KeperHoogte / 2;
  const yPos = Hoogte + PlaatDikte - hoogteStaander / 2;
  const zPos = StartZ + i * Spacing;
  const xOffset = (Breedte + PlankDikte) / 2 + PlaatDikte;

  for (let richting of [1, -1]) {
    const ZijkantStaander = createBoxWithEdges(
      new THREE.BoxGeometry(PlankDikte, hoogteStaander, PlankBreedte),
      PlankColorMat
    );

    ZijkantStaander.position.set(richting * xOffset, yPos, zPos);

    scene.add(ZijkantStaander);
    ZijkantStaanders.push(ZijkantStaander);
  }
}

       if (document.getElementById("VerstevigingCheck").checked){

    DekselLiggerBreedte = Breedte + 2 * PlaatDikte + 2 * PlankDikte;

  } else {

        DekselLiggerBreedte = Breedte + 2 * PlaatDikte;

      ZijkantStaanders.forEach(ZijkantStaander => {
      ZijkantStaander.visible = false;
  });
  
}

}


function Deksel_GEN(){


  let Deksel = createBoxWithEdges(new THREE.BoxGeometry(Breedte + 2*PlaatDikte, PlaatDikte, Lengte + 2* KopDikte), OSBColorMat);
  Deksel.position.set(0,Hoogte + PlaatDikte/2,0);
  Deksels.push(Deksel)
  scene.add(Deksel);

  

  //---------versteveging deksel-----------------
  
    for (let i = 0; i < KeperAantal; i++) {
      let zPos = StartZ + i * Spacing;
  
      let Ligger = createBoxWithEdges(new THREE.BoxGeometry(DekselLiggerBreedte,PlankDikte,PlankBreedte), PlankColorMat);
  
      // Positioneren onder de kist
      Ligger.position.set(0,Hoogte + PlankDikte/2 + PlaatDikte, zPos);
  
      // Toevoegen aan de scene en de array
      scene.add(Ligger);
      DekselLiggers.push(Ligger);
  
  }
}


//-----------------schaduwvlak toevoegen------------------------
if (ground) scene.remove(ground);
ground = new THREE.Mesh(new THREE.PlaneGeometry(30, 30), new THREE.ShadowMaterial({color: 0xD3D3D3}));
ground.rotation.x = -Math.PI / 2;
ground.receiveShadow = true;
ground.position.y = -KeperHoogte;
scene.add(ground);


  function UpdateModel(){

    OnderdelenDeleten();
    VariabelenInlezen();
    Bodem_GEN();
    Keper_GEN();
    Koppen_GEN();
    ZijKanten_GEN();
    
  if (document.getElementById('DekselVerbergenCheck').checked==false){
    Deksel_GEN();
  }

}

//---------animatie starten------------
function animate() {
    requestAnimationFrame(animate);
    controls.update();
    renderer.render(scene, camera);
    
  }
  animate();
  
  //-------------kist updaten bij aanpassing-----------
function setupEventListeners() {
  const elements = document.querySelectorAll("input, select, textarea");

  elements.forEach((el) => {
    if (el.tagName === "INPUT") {
      if (el.type === "range") {
        // Sliders live update bij input
        el.addEventListener("input", UpdateModel);
      } else if (el.type === "text" || el.type === "number") {
        // Tekst/nummer: update bij blur of Enter
        el.addEventListener("blur", UpdateModel);
        el.addEventListener("keydown", (e) => {
          if (e.key === "Enter") UpdateModel();
        });
      } else if (el.type === "checkbox") {
        // Checkbox update bij change
        el.addEventListener("change", UpdateModel);
      }
    } else if (el.tagName === "SELECT") {
      // Select update bij change
      el.addEventListener("change", UpdateModel);
    }
  });
}





  // Start eventlisteners
  setupEventListeners();
  
  // Roep UpdateKist één keer aan om de initiële staat van de kist te tekenen

VariabelenInlezen();
UpdateModel();